export const TOO_FEW_PARAMETERS = 'TOO_FEW_PARAMETERS'; //Used when insufficient amount of params is provided for component registration.
export const INVALID_PARAMETERS = 'INVALID_PARAMETERS'; //Used when parameters provided for component registration are invalid.
export const COMP_NOT_REGISTERED = 'COMP_NOT_REGISTERED'; //Used when component is encountered in DOM but it is not registered.
export const DOM_CONTEXT_MISSING = 'DOM_CONTEXT_MISSING'; //Used when the DOM context of a component is missing.
export const SERVER_ERROR = 'SERVER_ERROR'; //Used when there is an error from the server side.
